/* =================================== */
/* 섹션별 상세 스타일          */
/* =================================== */

/* Section 1: Hero */
.hero-section {
  display: flex;
  justify-content: space-between;
  background: linear-gradient(303deg, #4f46e5 13.1%, #0711d9 91.43%);
  positon: relative;
  overflow: hidden;
  gap: 0px;
}
.hero-container {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  margin: 0 auto;
}
.hero-wrap {
  position: relative;
  width: 700px;
  height: 700px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.hero-bg {
  position: absolute;
  inset: -20%;
  background-size: cover;
  background-position: center;
  transform: scale(1.1);
  border-radius: 50%;
  filter: blur(80px);
  object-fit: cotain;
  -webkit-mask-image: radial-gradient(circle, black 40%, transparent 80%);
  mask-image: radial-gradient(circle, black 40%, transparent 80%);
  z-index: 0;
}
.hero-bg::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  opacity: 0.5;
  border: none;
  object-fit: contain;
  background: radial-gradient(
    circle at center,
    #f58524 0%,
    #dfec26 100%,
    transparent 100%
  );
}
.hero-wrap img {
  position: relative;
  z-index: 1;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.hero-content p {
  max-width: 760px;
  margin-bottom: 70px;
  margin-top: 20px;
  font-size: 1rem;
}
.docs {
  cursor: pointer;
}

.link {
  padding-top: 44.4px;
  padding-bottom: 44.4px;
  color: #fff;
}
.toServiceButton {
  display: flex;
  width: 395px;
  height: 80px;
  padding: 30px 70px;
  justify-content: center;
  align-items: center;
  gap: 7.2px;
  color: #fff;
  font-family: Poppins;
  font-size: 18px;
  font-style: normal;
  font-weight: 700;
  line-height: 100%; /* 18px */
  text-transform: uppercase;
  background: #f77704;
}

.hero-content button {
  background: linear-gradient(90deg, #f77705 0%, #f79c05 100%);
  color: #ffffff;
  border: none;
  font-weight: bold;
  cursor: pointer;
}

.hero-content button:hover {
  background: linear-gradient(90deg, #00e1ff60 0%, #f79c05 100%);
}

.slider-container {
  width: 100%; /* 부모 너비에 맞게 조정 */
  overflow: hidden; /* 슬라이더 트랙을 벗어나는 이미지를 숨김 */
  position: relative; /* 필요에 따라 */
  padding: 65px 0 65px 0;
}

.slider-track {
  display: flex;
  width: fit-content; /* 모든 이미지를 일렬로 나열하기 위해 콘텐츠 너비에 맞춤 */
  animation: slide-animation 30s linear infinite; /* 애니메이션 적용 */
  /* 30s는 예시 시간. 이미지 개수와 이동 속도에 따라 조절하세요. */
  /* linear: 일정한 속도로 이동 */
  /* infinite: 무한 반복 */
}

.slider-track img {
  flex-shrink: 0; /* 이미지들이 줄어들지 않도록 고정 */
  width: calc(100vw / 5); /* 한 화면에 5개의 이미지가 보이도록 설정 */
  /* 또는 부모 컨테이너 너비 기준으로: width: calc(100% / 5); */
  height: auto; /* 비율 유지 */
  display: block; /* 이미지 하단 여백 제거 */
  /* gap을 사용하지 않으므로, 이미지 사이에 여백이 자동으로 생기지 않습니다. */
  /* 필요하다면 이미지 자체에 margin-right를 줘서 조절할 수 있습니다. */
}

/* 뷰포트 너비에 따라 이미지 너비 조절 (반응형) */
@media (max-width: 1200px) {
  .slider-track img {
    width: calc(100vw / 4); /* 1200px 이하에서는 4개 표시 */
  }
}

@media (max-width: 768px) {
  .slider-track img {
    width: calc(100vw / 3); /* 768px 이하에서는 3개 표시 */
  }
}

@media (max-width: 480px) {
  .slider-track img {
    width: calc(100vw / 2); /* 480px 이하에서는 2개 표시 */
  }
}

@keyframes slide-animation {
  0% {
    transform: translateX(0); /* 시작 위치 */
  }
  100% {
    /* 이미지 15개 + 복사본 5개 = 총 20개 이미지 */
    /* 5개 이미지 너치만큼 왼쪽으로 이동하여 원본 이미지 5개가 사라지도록 */
    /* (15개의 이미지 중 5개를 건너뛰도록) */
    /* 또는 총 이미지 개수 (15개) 만큼 움직이도록 설정 */
    /* 여기서는 (전체 이미지 개수 / 화면에 보이는 이미지 개수) * 100% 로 계산 */
    /* (15 / 5) = 3 -> -300% (슬라이더 트랙 전체 너비의 1/3 이동) */
    /* 더 정확하게는, 원본 15개 이미지의 총 너비만큼 이동해야 합니다. */
    /* 여기서는 한 개의 이미지 너비 * 15 (원본 이미지 개수) 만큼 이동 */
    transform: translateX(calc(-1 * (100vw / 5) * 15));
    /* 예시: 5개 이미지가 한 화면에 보이고, 원본 이미지가 15개일 때, */
    /* 15개 이미지의 총 너비만큼 움직이면 한 바퀴를 돕니다. */
    /* 100vw / 5는 하나의 이미지 너비입니다. */
  }
}

/* 마우스 오버 시 애니메이션 일시 정지 (선택 사항) */
.slider-track:hover {
  animation-play-state: paused;
}

/* Section 2: Analyze */
.analyze-section {
  background: #f4f4f4;
  flex-direction: column;
}

/* 공통 스타일 (텍스트를 클립하고 투명하게 만드는 부분) */
.gradient-text-default,
.gradient-text-precision {
  background-clip: text !important;
  -webkit-background-clip: text !important;
  -webkit-text-fill-color: transparent !important;
  display: inline-block; /* span 요소가 background-clip을 적용받도록 */
  white-space: nowrap; /* 줄바꿈 방지 */
  text-align: center;
  font-family: Poppins;
  font-size: 50px;
  font-style: normal;
  font-weight: 600;
  line-height: 100%; /* 50px */
  text-transform: capitalize;
}

/* 기본 글씨에 적용될 그라데이션 */
.gradient-text-default {
  background: linear-gradient(264deg, #f77705 16.11%, #f79c05 77.7%);
  /* 다른 폰트 스타일도 여기에 포함될 수 있습니다. */
}

/* "Precision"에만 적용될 그라데이션 */
.gradient-text-precision {
  background: linear-gradient(262deg, #4f46e5 31.59%, #0711d9 61.58%);
  /* 다른 폰트 스타일도 여기에 포함될 수 있습니다. */
}

.gradient-text {
  color: #121139;
  text-align: center;
  font-family: Poppins;
  font-size: 22px;
  font-style: normal;
  font-weight: 500;
  line-height: 130%; /* 28.6px */
  letter-spacing: 0.88px;
  margin: 0 auto;
  margin-bottom: 120px;
}

.dashboard-image {
  width: 100%;
  max-width: 960px;
  max-height: 540px;
  aspect-ratio: 16 / 9; /* 16:9 비율 유지 */
  margin: 0 auto; /* 이 div 자체를 부모에 대해 중앙 정렬 */
  margin-bottom: 180px;

  /* --- 이미지 중앙 정렬을 위한 핵심 코드 --- */
  display: flex; /* Flexbox 컨테이너로 만듭니다. */
  justify-content: center; /* 가로(수평) 중앙 정렬 */
  align-items: center; /* 세로(수직) 중앙 정렬 */
  /* ------------------------------------- */
}

/* 이미지 자체의 margin: auto;는 이제 필요 없습니다. */
.dashboard-image img {
  /* 이미지가 컨테이너를 벗어나지 않도록 최대 크기 지정 */
  width: 960px; /* 컨테이너 너비에 맞춤 */
  height: auto; /* 비율 유지 */
  object-fit: contain; /* 이미지가 잘리지 않고 전체가 보이도록 비율 유지 */
}

/* Section 3: Value */
.value-section {
  background: linear-gradient(303deg, #4f46e5 13.1%, #0711d9 91.43%);
  flex-direction: row;
  display: flex;
  padding-top: 125px;
  justify-content: center;
  align-items: center;
  gap: 100px;
}

.value-content-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  gap: 20px;
  margin: 0 auto 200px auto; /* 아래 여백 추가 */
  flex-wrap: wrap; /* 모바일 화면에서 줄바꿈 허용 */
}
.value-image {
  flex: 1;
  margin: 0 auto;
  justify-content: center;
  align-items: center;
  display: flex; /* 이미지 중앙 정렬을 위해 Flexbox 컨테이너로
    object-fit: cover; /* 이미지 비율 유지 */
}

.value-content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  flex: 1;
  color: #fff;
  font-size: 20px;
  font-style: normal;
  max-width: 1000px;
  font-weight: 500;
  line-height: 130%; /* 28.6px */
  letter-spacing: 0.8px;
  min-width: 680px; /* 최소 너비 설정 */
}

.value-content h2 {
  text-align: left;
  font-size: 30px;
  font-weight: 600;
  text-transform: capitalize;
  line-height: 100%;
  padding-bottom: 25px;
}

/* Section 4: Key Features */
.key-features-section {
  align-items: center;
  flex-direction: column;
  background: #f4f4f4;
  padding-top: 150px;
  position: relative;
  padding-bottom: 200px;
}

.key-features-description {
  color: #121139;
  text-align: center;
  font-family: Poppins;
  font-size: 22px;
  font-style: normal;
  font-weight: 500;
  line-height: 130%; /* 28.6px */
  letter-spacing: 0.88px;
  margin: 45px auto;
}
.features-content {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  flex-wrap: wrap;
  padding: 100px 50px 0 50px;
}

.features-main-image {
  flex: 2;
  height: auto; /* 높이는 내용에 따라 자동으로 조정 */
  display: flex;
  justify-content: center;
  align-items: center;
}

.features-main-image img {
  max-width: 100%; /* 부모 컨테이너(features-main-image) 너비에 맞춤 */
  height: auto; /* 비율 유지 */
  display: block; /* margin auto를 적용받기 위해 블록 요소로 만듦 */
  margin: 0 auto; /* 이미지 자체를 가로 중앙 정렬 (선택 사항이지만 안전하게) */
  object-fit: contain; /* 이미지가 잘리지 않고 전체가 보이도록 비율 유지 */
}

.features-list {
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 55px;
  text-aling: center;
}

/* 모바일 이미지 컨테이너 */
.features-mobile-image {
  position: absolute; /* 부모 .features-main-image를 기준으로 위치 */
  top: 55%; /* 세로 중앙 */
  left: 50%; /* 가로 중앙 */
  transform: translate(0%, 0%) translate(0%, -50%); /* 중앙 정렬을 위해 이동 */
  z-index: 10; /* 메인 이미지 위에 나타나도록 높은 z-index 설정 */
  height: auto; /* 높이 자동 조정 */
}
@media (max-width: 1400px) {
  .features-mobile-image {
    top: 60%; /* 세로 중앙 */
    left: 50%; /* 가로 중앙 */
    transform: translate(0%, 0%) translate(0%, -50%); /* 중앙 정렬을 위해 이동 */
  }
  .features-mobile-image img {
    width: 150px; /* 모바일 기기 너비에 맞게 최대 너비 설정 (사진 기반 추정) */
    height: auto; /* 높이 자동 조정 */
  }
}

/* features-mobile-image 내의 img 태그 */
.features-mobile-image img {
  height: auto; /* 비율 유지 */
  object-fit: contain; /* 이미지가 잘리지 않고 전체가 보이도록 비율 유지 */
  width: 300px;
}

.feature-item {
  color: #fff;
  font-family: Poppins;
  font-size: 20px;
  font-style: normal;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 700;
  line-height: 130%; /* 26px */
  letter-spacing: 0.8px;
  border: 1px solid #ffffff;
  height: 120px;
  width: 100%;
  padding: 0 20px 0 20px;
  border-radius: 30px;
  background: #f77705;
  z-index: 1; /* 다른 요소 위에 나타나도록 설정 */
}

/* Section 5: AI Reports */
.ai-reports-section {
  background: linear-gradient(303deg, #4f46e5 13.1%, #0711d9 91.43%);
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 100px;
  text-align: center;
}

.ai-reports-description {
  color: #fff;
  text-align: center;
  font-family: Poppins;
  font-size: 22px;
  font-style: normal;
  font-weight: 500;
  line-height: 130%; /* 28.6px */
  letter-spacing: 0.88px;
  margin: 50px auto 70px auto; /* 아래 여백 추가 */
}

.report-content {
  display: grid;
  grid-template-columns: 1fr minmax(40%, 60%) 1fr;
  width: 100%;
  margin: 30px auto 100px auto;
  align-items: center;
  justify-content: center;
  position: relative;
}
.report-main-image {
  grid-column: 2;
  position: relative;
  justify-self: center;
  align-items: center;
  width: 100%;
  height: 450px;
  margin-bottom: 30px;
}
.report-content .feature-item {
  grid-column: 1;
  align-items: center;
  justify-self: end;
  transform: translateX(-5%);
}
.report-features {
  grid-column: 3;
  display: flex;
  flex-direction: column;
  gap: 10vw;
}

/* =================================== */
/* 모바일 반응형 스타일         */
/* =================================== */

@media (max-width: 1024px) {
  h1 {
    font-size: 2.5rem;
  }
  h2 {
    font-size: 2rem;
  }
  .section {
    flex-direction: column;
    padding: 60px 5%;
  }
  .hero-image {
    width: 50%; /* 모바일 화면에 맞게 크기 조정 */
    height: auto; /* 비율 유지 */
  }
  .hero-section {
    text-align: center;
    position: relative;
    overflow: hidden;
  }
  .hero-content {
    align-items: center;
    display: flex;
    flex-direction: column;
  }

  .hero-wrap {
    display: none;
  }

  .analyze-section {
    padding-bottom: 0px;
  }

  .value-section {
    padding-top: 100px;
    padding-bottom: 0px;
    flex-direction: column; /* 텍스트가 위로 오도록 순서 변경 */
    text-align: center;
  }
  .value-content h2 {
    text-align: center;
  }

  .footer {
    flex-direction: column;
    text-align: center;
  }

  .dashboard-image img {
    width: 700px; /* 모바일 화면에 맞게 크기 조정 */
    height: auto; /* 비율 유지 */
    margin: 0 auto;
    aspect-ratio: 16 / 9; /* 16:9 비율 유지 */
    margin-bottom: 180px;
    object-fit: cover; /* 이미지 비율 유지 */
  }
  .value-content-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 130px;
    margin: 0 auto 320px auto; /* 아래 여백 추가 */
  }

  .features-main-image {
    flex: auto; /* Flex 비율 제거 */
    width: 90%; /* 화면 너비에 맞게 조정 */
    max-width: 500px; /* 최대 너비 제한 */
    height: auto; /* 높이 자동 조정 */
  }

  .features-list {
    flex: auto; /* Flex 비율 제거 */
    width: 90%; /* 화면 너비에 맞게 조정 */
    max-width: 400px; /* 최대 너비 제한 */
    gap: 15px; /* 모바일에서 간격 조정 */
  }

  .feature-item {
    font-size: 18px; /* 모바일에서 폰트 크기 조정 */
    height: 100px; /* 모바일에서 높이 조정 */
  }
  .features-content {
    flex-direction: column; /* 세로로 쌓이도록 변경 */
    align-items: center;
    gap: 40px;
  }

  /* 모바일에서는 features-mobile-image의 위치와 크기를 더 적절하게 조정 */
  .features-mobile-image {
    position: relative; /* 모바일에서는 absolute 해제하고 일반 흐름으로 돌아오게 */
    top: auto;
    left: auto;
    transform: none; /* transform 초기화 */
    order: 3; /* 가장 위로 오도록 순서 변경 (선택 사항) */
    margin-top: 50px; /* 아래 요소들과 간격 */
    max-width: 250px; /* 모바일에서 적절한 크기 */
  }
  .report-content {
    grid-template-columns: 1fr;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 50px;
    margin: 0 auto;
  }
  .feature-left,
  .report-features,
  .report-main-image {
    width: 100%;
    transform: none;
